// import React from "react";
// import Link from "next/link";
// import { db } from "~/server/db";
// import { Pacifico, Montserrat } from 'next/font/google';
// import { montserrat, pacifico } from "~/lib/font";
// import { redirect } from "next/navigation";
// import { auth } from "~/server/auth";
// import Pagination from "~/app/ui/pagination";

export async function ProductList(  searchParams?: {
  size?: string;
  page?: string;
  userId?: string;
}) {
  const count = await db.user.count();
  const session = await auth();

  // Загружаем продукты вместе с изображениями
  const page = Number(searchParams?.page ?? "1");
  const size = Number(searchParams?.size ?? "16");
  const pages = Math.ceil(count / size);
  if (!session?.user?.email) {
    redirect("/api/auth/signin");
  }
  const products = await db.product.findMany({
    include: {
      images: true,
    },
  });

  if (products.length === 0) {
    return <p className={`text-center text-gray-500 ${montserrat.className}`}>Нет доступных товаров</p>;
  }

  return (
    <div className={`${montserrat.variable} font-sans p-4`}>
      <h1 className={`${pacifico.className} text-4xl text-center mb-8 text-transparent bg-clip-text bg-gradient-to-r from-purple-600 to-pink-600`}>
        Наши Товары
      </h1>

      <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
        {products.map((product) => {
          // Берём первое изображение или плейсхолдер
          const imageUrl = product.images[0]?.url || "/placeholder-product.jpg";

          return (
            <Link
              key={product.id}
              href={`/product/${product.id}`}
              className="rounded-xl border border-gray-200 p-5 shadow-sm hover:shadow-lg transition-all duration-300 block hover:border-purple-200"
            >
              <div className="aspect-square relative w-full mb-4 rounded-lg overflow-hidden bg-gray-50">
                <img
                  src={imageUrl}
                  alt={product.name}
                  className="w-full h-full object-cover transition-transform duration-500 hover:scale-105"
                />
              </div>
              <h2 className={`text-xl font-bold line-clamp-2 hover:text-purple-600 transition-colors font-serif`}>
                {product.name}
              </h2>
              <p className={`text-sm text-gray-600 line-clamp-3 mb-3 mt-2 italic`}>
                {product.description}
              </p>
              <p className={`font-bold text-purple-600 text-lg ${pacifico.className}`}>
                {product.price.toFixed(2)} ₽
              </p>
            </Link>
          );
        })}
      </div>
      {page >1 &&<Pagination totalPages={pages} /> }
      

    </div>
    
  );
}



